






































import {
  defineComponent, reactive, computed, ref, toRefs,
} from '@vue/composition-api';
import Header, { OperateType } from './Header.vue';
import Edit from './Edit.vue';
import Review from './review/index.vue';
import Empty from './Empty.vue';
import api from './api';

export type Obj = {[any:string]:any};

export type Item = Obj;

export type Col = {
  id?:string,
  name:string,
  temName:string,
  itemInfos:Item[],
  isInput?:boolean
};

export default defineComponent({
  name: 'meet-home',
  components: {
    Header, Edit, Review, Empty,
  },
  props: {},
  setup(props, { refs, emit, root }) {
    const state = reactive({
      operateType: 'review',
      allCols: [],
      showCols: [] as any[],
      colsNum: 8,
      currentCol: {},
      searchValue: '',
      isSearch: false,
    });
    const isEdit = computed(() => state.operateType === 'edit');
    const handleTypeChange = (operateType:OperateType) => {
      state.operateType = operateType;
    };
    const cancelEdit = () => {
      state.operateType = 'review';
    };
    const commitEdit = () => {
      state.operateType = 'review';
    };

    const search = (searchValue:string) => {
      state.searchValue = searchValue;
      state.isSearch = true;
    };

    const colsChange = (showCols:any[]) => {
      state.showCols = showCols;
    };

    const colsNumChange = (colsNum:4|8|16) => {
      state.colsNum = colsNum;
    };

    const openColList = (currentCol:any) => {
      state.currentCol = currentCol;
    };

    const resetSearch = () => {
      state.isSearch = false;
      state.searchValue = '';
    };

    const toCols = () => {
      state.currentCol = {};
      resetSearch();
    };

    const toEdit = () => {
      state.operateType = 'edit';
    };

    const init = async () => {
      state.allCols = (await api.fetchReviewCols({ meetingId: '123', meetingColumnSize: 10000, meetingColumnPage: 1 }))?.records || [];
    };

    init();


    return {
      ...toRefs(state),
      isEdit,
      handleTypeChange,
      cancelEdit,
      commitEdit,
      search,
      colsChange,
      colsNumChange,
      openColList,
      toCols,
      toEdit,
    };
  },
});
