import axios from '@/utils/axios';

type Obj = {[any:string]:any};
const api = {
  // 删除项
  async delItem(params:{id:string}) {
    const result = (await axios({
      url: '/meeting/column/item/delete',
      method: 'post',
      data: params,
    })) || false;

    return result;
  },
  // 移动项
  async moveItem(params:{id:string, meetingColumnId:string, index:number}) {
    const result = (await axios({
      url: '/meeting/column/item/move',
      method: 'post',
      data: params,
    })) || false;

    return result;
  },

  // 查看模式列表
  async fetchReviewCols(params:{meetingId:string, meetingColumnIds?:string[], meetingColumnPage?:number, meetingColumnSize?:number}) {
    const result = (await axios({
      url: '/meeting/conference/console',
      method: 'post',
      data: params,
    })).data || {};

    return result;
  },
  // 编辑模式列表
  async fetchEditCols(params:{id:string}) {
    const result = (await axios({
      url: '/meeting/conference/console/editable',
      method: 'post',
      data: params,
    })).data || [];

    return result;
  },
  // 发布
  async release(params:{id:string}) {
    const result = (await axios({
      url: '/meeting/conference/console/release',
      method: 'post',
      data: params,
    })) || false;

    return result;
  },
  // 新增列
  async addCol(params:{ name:string, sort:number, meetingId:string}) {
    const result = (await axios({
      url: '/meeting/column/add',
      method: 'post',
      data: params,
    }));

    return result;
  },
  // 删除列
  async delCol(params:{columnId:string}) {
    const result = (await axios({
      url: '/meeting/column/delete',
      method: 'post',
      data: params,
    }));

    return result;
  },
  // 修改列
  async editCol(params:{id:string, name:string, sort:number, meetingId:string}) {
    const result = (await axios({
      url: '/meeting/column/update',
      method: 'post',
      data: params,
    }));

    return result;
  },
};

export default api;
