








































































































import {
  defineComponent, reactive, computed, ref, toRefs, watch,
} from '@vue/composition-api';
import {
  DragContainer, DragList, DragItem, getId,
} from '@/components/drags';
import OverText from '@/components/OverText.vue';
import { useListDrag } from './useListDrag';
import api from './api';

import { Item, Col } from './index.vue';

export default defineComponent({
  name: 'meet-home__edit',
  components: { DragContainer, DragList, OverText },
  props: {
    cols: {
      type: Array,
      default: () => [],
    },
    meetingId: {
      type: String,
      default: '123',
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({
      currentCols: [] as any[],
      itemDraging: false,
    });
    const {
      listStartIndex, listEnterIndex, listDraging, listDragStart, listDragEnter, listDragEnd,
    } = useListDrag();

    watch(() => props.cols, () => {
      state.currentCols = JSON.parse(JSON.stringify(props.cols));
    }, { immediate: true });

    const addItem = (list:Item[], listIndex:number) => {
      const _hid = getId();
      const newItem = {
        _hid,
        title: _hid,
      };
      list.push(newItem);
    };

    const editItem = (item:Item) => {

    };

    const delItem = (item:Item) => {

    };

    const addCol = async (index:number) => {
      const name = `新增列${getId()}`;
      const newList = {
        name,
        temName: name,
        list: [],
        isInput: false,
      };
      await api.addCol({ name: newList.name, sort: index, meetingId: props.meetingId });
      state.currentCols.splice(index + 1, 0, newList);
      await root.$nextTick();
      refs?.dragContainer.init();
    };

    const delCol = (index:number) => {
      state.currentCols.splice(index, 1);
    };

    const editColTitle = async (col:Col, i:number) => {
      col.isInput = true;
      await root.$nextTick();
      refs?.input?.[i].focus();
    };

    const blurColTitle = async (col:any, i:number) => {
      col.isInput = false;
      await api.editCol({
        id: col.id!, name: col.temName, sort: i + 1, meetingId: props.meetingId,
      });
      col.name = col.temName;
    };

    const init = async () => {
      const cols:Col[] = await api.fetchEditCols({ id: props.meetingId });
      state.currentCols = cols.map(item => ({ ...item, isInput: false }));
    };

    init();

    return {
      ...toRefs(state),
      addItem,
      editItem,
      delItem,
      addCol,
      delCol,
      listStartIndex,
      listEnterIndex,
      listDragStart,
      listDragEnter,
      listDragEnd,
      listDraging,
      editColTitle,
      blurColTitle,
    };
  },
});
