














import {
  defineComponent, reactive, computed, ref, toRefs,
} from '@vue/composition-api';

export default defineComponent({
  name: 'meet-review__empty',
  components: {},
  props: {},
  setup(props, { refs, emit, root }) {
    const state = reactive({});
    const toEdit = () => {
      emit('to-edit');
    };
    return {
      ...toRefs(state),
      toEdit,
    };
  },
});
